export enum AppStateEnum {
  ACTIVE = 'ACTIVE',
  DEACTIVE = 'DEACTIVE'
};

export enum AppPricingEnum {
  FREE = 'FREE',
  PRODUCTION = 'PRODUCTION',
  ADVANCED = 'ADVANCED',
  ENTERPRISE = 'ENTERPRISE'
};

export enum AppUserRoleEnum {
  OWNER = 'OWNER',
  DEVELOPER = 'DEVELOPER',
  MANAGER = 'MANAGER'
};

export enum ChatMemberRoleEnum {
  OWNER = 'OWNER',
  MEMBER = 'MEMBER',
  OBSERVER = 'OBSERVER'
};

export enum MemberPermissionType {
  NONE = 'NONE',
  READ = 'READ',
  WRITE = 'WRITE',
  ADMIN = 'ADMIN',
  SUPER = 'SUPER',
};


export enum MessageTypeEnum {
  TEXT = 'TEXT',
  FILE = 'FILE',
  JSON = 'JSON'
};

export enum MessageByEnum {
  USER = 'USER',
  ADMIN = 'ADMIN',
  SYSTEM = 'SYSTEM'
};

export type AppType = {
  id: string
  name: string
  server: string
  apiKey: string
  firebaseCredentials: FirebaseCredentialsType | null
  state: AppStateEnum
  pricing: AppPricingEnum
  image: string
  language: string
  country: string
  enableTranslation: boolean
  enableJoinMessage: boolean
  enableLeaveMessage: boolean
  enableInviteMessage: boolean
  enableExcludeMessage: boolean
  enableImageUpload: boolean
  enableVideoUpload: boolean
  chatPageLimit: number
  chatListPageLimit: number
  thumbnailSize: number
  notificationSound: string
  maxImageSize: number
  maxVideoSize: number
  multipleUploadCount: number
  createdAt: Date
  updatedAt: Date
  deletedAt: Date | null
}

export type AppUserType = {
  role: AppUserRoleEnum
  createdAt: Date
  updatedAt: Date
  AppId: string
  UserId: string
}

export type UserType = {
  id: string
  email: string
  password: string
  name: string
  avatar: string
  verification: boolean
  tfaEnable: boolean
  token: string
  tfa: string
  visitedAt: Date
  createdAt: Date
  updatedAt: Date
  deletedAt: Date | null
}

export type ChatType = {
  id: string
  private: boolean
  open: boolean
  name: string
  image: string
  lastMessage: MessageType | null
  data: object | null
  distinctKey: string
  group: string
  maxMember: number
  createdAt: Date
  updatedAt: Date
  deletedAt: Date | null
  AppId: string
  Members: Array<ChatMemberType>
}

export type ChatMemberType = {
  missedCount: number
  role: ChatMemberRoleEnum
  group: string
  createdAt: Date
  updatedAt: Date
  ChatId: string
  MemberId: string,
  Member: MemberType,
  AppId: string
}

export type MemberType = {
  id: string
  name: string
  language: string
  country: string
  avatar: string
  deviceToken: string
  device: DeviceType | null
  group: string
  permission: MemberPermissionType
  data: object | null
  createdAt: Date
  updatedAt: Date
  deletedAt: Date | null
  AppId: string
}

export type MessageType = {
  id: string
  text: string | null
  files: Array<{ uri: string }> | null
  json: object | null
  type: MessageTypeEnum
  translation: ITranslationIndexSignature | null
  by: MessageByEnum
  readReceipt: number
  createdAt: Date
  updatedAt: Date
  deletedAt: Date | null
  AppId: string | null
  ChatId: string
  SenderId: string | null
}

export type InvitationType = {
  role: AppUserRoleEnum
  accept: boolean
  email: string
  inviter: string
  createdAt: Date
  updatedAt: Date
  AppId: string
}


