export type DeviceType = {
  platform: string,
  language: string,
  product: string,
  userAgent: string,
  sdkVersion: string,
};

export type FileType = {
  uri: string,
  type: string,
  name?: string
}

export type FirebaseCredentialsType = {
  privateKey: string,
  clientEmail: string,
  projectId: string,
}

export interface ISystemMessagesIndexSignature {
  [key: string]: string
}

export interface IAppIndexSignature {
  [key: string]: object | string | boolean | number | Date | null
}

export interface ITranslationIndexSignature {
  [key: string]: string
}

/**
 * Default System Message
 * system message type true  system message created  chat Member silent push  
 * invite  exclude   push  .
 * join  leave    chat member  silent push 
 */
export const DefaultSystemMessages: ISystemMessagesIndexSignature = {
  invite: '{MEMBER-NAME} was invited to chat',
  exclude: '{MEMBER-NAME}  was excluded from chat',
  join: '{MEMBER-NAME}  joined chat',
  leave: '{MEMBER-NAME}  left chat'
};

export const SupportedImageFormat = ['image/png', 'image/jpg', 'image/jpeg', 'image/gif', 'image/webp'];
export const SupportedVideoFormat = ['video/mp4', 'video/webm'];

export enum ChattyNotificationType {
  CHATTY_NEW_MESSAGE = 'CHATTY_NEW_MESSAGE',
  CHATTY_SYSTEM_MESSAGE = 'CHATTY_SYSTEM_MESSAGE',
}

export interface IChatConnectPayload {
  at?: string | undefined;
  with?: string | string[] | undefined;
  distinctKey?: string | null | undefined;
  private?: boolean | undefined;
  name?: string | undefined;
  image?: string | undefined;
  data?: {} | undefined;
  group?: string | undefined;
}

export interface IFetchChatListPayload {
  refresh: boolean | undefined;
  group: string | undefined;
}

export interface IUpdateChatListPayload {
  ChatId: string;
}

export interface IMissedCount {
  missedCount: {
    total: number,
    group: Array<{ name: string, count: number }>
  }
}

export type ErrorResponseType = { message: string };
export type ChatConnectResponseType = ChatType & { error: ErrorResponseType };
export type ChatDisconnectResponseType = {} & { error: ErrorResponseType };
export type ChatListConnectResponseType = {} & { error: ErrorResponseType };
export type ChatListDisconnectResponseType = {} & { error: ErrorResponseType };
export type MessageSendResponseType = MessageType & { error: ErrorResponseType };
export type MessagesFetchResponseType = { refresh: boolean, hasNext: boolean, messages: Array<MessageType> } & { error: ErrorResponseType };
export type MessageReceiveResponseType = MessageType & { error: ErrorResponseType };
export type MessagesUpdateResponseType = Array<MessageType> & { error: ErrorResponseType };
export type ChatListFetchResponseType = { refresh: boolean, hasNext: boolean, chats: Array<ChatType> } & { error: ErrorResponseType };
export type ChatListUpdateResponseType = { id: string } & { error: ErrorResponseType };
export type ChatRefreshResponseType = ChatType & { error: ErrorResponseType };

/** Chat Handlers */
export type onChatConnect = (data: ChatConnectResponseType) => void;
export type onChatRefresh = (data: ChatRefreshResponseType) => void;
export type onMessagesFetch = (data: MessagesFetchResponseType) => void;
export type onMessageSend = (data: MessageSendResponseType) => void;
export type onMessageReceive = (data: MessageReceiveResponseType) => void;
export type onMessagesUpdate = (data: MessagesUpdateResponseType) => void;

/** ChatList Handlers */
export type onChatListConnect = (data: ChatListConnectResponseType) => void;
export type onChatListFetch = (data: ChatListFetchResponseType) => void;
export type onChatListUpdate = (data: ChatListUpdateResponseType) => void;



export interface IInitializePayloads {
  apiKey: string;
  member: Partial<MemberType>;
}


