import io, { Socket } from 'socket.io-client';
import Chatty from '.';
import {
  ChattyEvent,
  IFetchChatListPayload,
  ChatListFetchResponseType,
  ChatListConnectResponseType,
  ChatListUpdateResponseType,
  onChatListConnect,
  onChatListFetch,
  onChatListUpdate,
  IUpdateChatListPayload
} from './Types';

declare const MODE: string;

const dev = MODE === 'development' ? 'dev' : '';

export type ChattyListParams = {
  onChatListConnect?: onChatListConnect,
  onChatListFetch?: onChatListFetch,
  onChatListUpdate?: onChatListUpdate
}

export class ChattyList {
  socket: Socket | undefined;

  onChatListConnect: onChatListConnect | undefined;
  onChatListFetch: onChatListFetch | undefined;
  onChatListUpdate: onChatListUpdate | undefined;

  constructor(payload: ChattyListParams) {
    this.onChatListConnect = payload.onChatListConnect;
    this.onChatListFetch = payload.onChatListFetch;
    this.onChatListUpdate = payload.onChatListUpdate;
  }

  connect() {
    const url: string = MODE === 'none' ? 'ws://localhost:4400' : `wss://${dev}socket.chatty-cloud.com`;

    this.socket = io(`${url}/chatlist.${Chatty.app?.name}`, {
      transports: ["polling", "websocket"],
      query: {
        MemberId: Chatty.member?.id!,
        AppId: Chatty.app?.id!
      },
      auth: {
        apikey: Chatty.apiKey
      }
    });
    this.addListener();
  }


  disconnect() {
    this.socket?.disconnect();
    this.removeListener();
    console.debug(':: ChattyList disconnected');
  }


  fetchChatList(payload: IFetchChatListPayload) {
    this.socket?.emit(ChattyEvent.FETCH_CHATLIST, payload);
  }


  updateChatList(payload: IUpdateChatListPayload) {
    this.socket?.emit(ChattyEvent.UPDATE_CHATLIST, payload);
  }


  addListener() {
    if (!this.socket) {
      console.warn(':: ChattyList socket is not connected');
      return;
    }


    this.socket.on(ChattyEvent.CONNECT_DONE, (data: ChatListConnectResponseType) => {
      console.debug(':: ChattyList CONNECT_DONE ChatList', data);
      this.onChatListConnect && this.onChatListConnect(data);
    });

    this.socket.on(ChattyEvent.CONNECT_FAIL, (data: ChatListConnectResponseType) => {
      console.warn(':: ChattyList CONNECT_FAIL', data?.error.message);
      this.onChatListConnect && this.onChatListConnect(data);
    });

    this.socket.on(ChattyEvent.FETCH_CHATLIST_DONE, (data: ChatListFetchResponseType) => {
      console.debug(':: ChattyList FETCH_CHATLIST_DONE', data);
      this.onChatListFetch && this.onChatListFetch(data);
    });

    this.socket.on(ChattyEvent.FETCH_CHATLIST_FAIL, (data: ChatListFetchResponseType) => {
      console.warn(':: ChattyList FETCH_CHATLIST_FAIL', data?.error.message);
      this.onChatListFetch && this.onChatListFetch(data);
    });

    this.socket.on(ChattyEvent.UPDATE_CHATLIST_DONE, (data: ChatListUpdateResponseType) => {
      console.debug(`:: ChattyList REFRESH_CHATLIST_DONE`, data);
      this.onChatListUpdate && this.onChatListUpdate(data);
    });

    this.socket.on(ChattyEvent.UPDATE_CHATLIST_FAIL, (data: ChatListUpdateResponseType) => {
      console.warn(`:: ChattyList REFRESH_CHATLIST_FAIL`, data?.error.message);
      this.onChatListUpdate && this.onChatListUpdate(data);
    });
  }


  removeListener() {
    if (!this.socket) {
      console.warn(':: ChattyList socket is not connected');
      return;
    }

    this.socket.off(ChattyEvent.CONNECT_DONE);
    this.socket.off(ChattyEvent.CONNECT_FAIL);

    this.socket.off(ChattyEvent.FETCH_CHATLIST_DONE);
    this.socket.off(ChattyEvent.FETCH_CHATLIST_FAIL);

    this.socket.off(ChattyEvent.UPDATE_CHATLIST_DONE);
    this.socket.off(ChattyEvent.UPDATE_CHATLIST_FAIL);

    this.socket = undefined;

  }
}