import io, { Socket } from 'socket.io-client';
import Chatty from '.';
import { ChattyList } from './ChattyList';
import {
  MessageType,
  ChattyEvent,
  MessagesFetchResponseType,
  IChatConnectPayload,
  ChatConnectResponseType,
  MessagesUpdateResponseType,
  MessageSendResponseType,
  MessageReceiveResponseType,
  ChatRefreshResponseType,
  FileType,
  MessageTypeEnum,
  MessageByEnum,
  onChatConnect,
  onMessageSend,
  onMessagesFetch,
  onMessageReceive,
  onMessagesUpdate,
  onChatRefresh
} from './Types';

declare const MODE: string;

const dev = MODE === 'development' ? 'dev' : '';

export type ChattyChatParams = {
  chattyList?: ChattyList,
  onChatConnect?: onChatConnect,
  onChatRefresh?: onChatRefresh
  onMessageSend?: onMessageSend,
  onMessagesFetch?: onMessagesFetch,
  onMessageReceive?: onMessageReceive,
  onMessagesUpdate?: onMessagesUpdate,
}

export class ChattyChat {
  private socket: Socket | undefined;
  private chattyList: ChattyList | undefined;
  // private id: string | undefined; // connected chat id

  private onChatConnect: onChatConnect | undefined;
  private onChatRefresh: onChatRefresh | undefined;
  private onMessageSend: onMessageSend | undefined;
  private onMessagesFetch: onMessagesFetch | undefined;
  private onMessageReceive: onMessageReceive | undefined;
  private onMessagesUpdate: onMessagesUpdate | undefined;


  constructor(payload: ChattyChatParams) {
    this.chattyList = payload.chattyList;
    this.onChatConnect = payload.onChatConnect;
    this.onChatRefresh = payload.onChatRefresh;
    this.onMessageSend = payload.onMessageSend;
    this.onMessagesFetch = payload.onMessagesFetch;
    this.onMessageReceive = payload.onMessageReceive;
    this.onMessagesUpdate = payload.onMessagesUpdate;
  }

  /**
   * 
   * @param payload 
   * 
   */
  connect(payload: IChatConnectPayload) {
    if (payload.with) {
      if (typeof payload.with === 'string') {
        payload.with = [payload.with];

      }
      payload.with = payload.with.filter(MemberId => MemberId && MemberId !== 'undefined' && MemberId !== 'null')
      payload.with.push(Chatty.member?.id!);

      // Remove duplicated elements
      payload.with = payload.with.filter((value, index, self) => self.indexOf(value) === index);

      if (payload.with.length < 2) {
        console.warn(':: ChattyChat connect with less than 2 members');
        return;
      }

      if (payload.distinctKey === undefined) {
        payload.distinctKey = Chatty.generateDistinctKey(payload.with)
      }
    }


    const url: string = MODE === 'none' ? 'ws://localhost:4400' : `wss://${dev}socket.chatty-cloud.com`;

    // option checker
    Object.keys(payload).forEach(key => !payload[key] && delete payload[key]);

    this.socket = io(`${url}/chat.${Chatty.app?.name}`, {
      transports: ["polling", "websocket"],
      query: {
        MemberId: Chatty.member?.id!,
        AppId: Chatty.app?.id!,
        at: payload.at,
        with: payload.with,
        distinctKey: payload.distinctKey,
        private: payload.private,
        name: payload.name,
        image: payload.image,
        data: payload.data && JSON.stringify(payload.data),
        group: payload.group
      },
      auth: {
        apikey: Chatty.apiKey
      },
      forceNew: true,
      // withCredentials: true, // cors "*"   .
    });


    this.addListener();
  }

  disconnect() {
    this.socket?.disconnect();
    this.removeListener();
    console.debug(':: ChattyChat disconnected');
  }

  fetchMessages(refresh: boolean) {
    this.socket?.emit(ChattyEvent.FETCH_MESSAGES, { refresh: refresh });
  }

  private sendMessage(data: Partial<MessageType>) {
    this.socket?.emit(ChattyEvent.SEND_MESSAGE, { ...data, retry: 5 });
  }

  refreshChat(ChatId: string) {
    // chat       
    this.socket?.emit(ChattyEvent.REFRESH_CHAT, { ChatId: ChatId });
  }

  markAsRead() {
    this.socket?.emit(ChattyEvent.MARK_AS_READ);
  }

  sendTextMessage(text: string) {
    const message: Partial<MessageType> = {
      id: 'xxxxxxxx-xxxx-4xxx-yxxx-xxxxxxxxxxxx'.replace(/[xy]/g, function (c) { var r = Math.random() * 16 | 0, v = c == 'x' ? r : (r & 0x3 | 0x8); return v.toString(16); }),
      createdAt: new Date(),
      text: text,
      type: MessageTypeEnum.TEXT,
      by: MessageByEnum.USER,
      SenderId: Chatty.member?.id!
    }
    this.sendMessage(message);

    // return temporary message object before inserting to database
    return message;
  }

  sendFileMessage(files: Array<FileType>) {
    if (!files) {
      console.warn(':: ChattyChat sendFileMessage function param error: files are undefined');
      return;
    }

    if (files.length > 4) {
      console.warn(':: ChattyChat sendFileMessage function param error: files length can not exceed 4');
      return;
    }

    const message: Partial<MessageType> = {
      id: 'xxxxxxxx-xxxx-4xxx-yxxx-xxxxxxxxxxxx'.replace(/[xy]/g, function (c) { var r = Math.random() * 16 | 0, v = c == 'x' ? r : (r & 0x3 | 0x8); return v.toString(16); }),
      createdAt: new Date(),
      files: files,
      text: 'File Message',
      type: MessageTypeEnum.FILE,
      by: MessageByEnum.USER,
      SenderId: Chatty.member?.id!
    }

    Chatty.uploadFiles(files)
      .then((files) => {
        console.info(':: ChattyChat uploadFiles success', files);
        this.sendMessage({ ...message, files: files });
      })
      .catch((err) => {
        console.warn(':: ChattyChat uploadFiles error', err.message);
      })

    // return temporary message object with SenderId before inserting to database
    return message;
  }







  private addListener() {
    if (!this.socket) {
      console.warn(':: ChattyChat socket is not connected');
      return;
    }

    this.socket.on(ChattyEvent.CONNECT_DONE, (data: ChatConnectResponseType) => {
      console.debug(`:: ChattyChat CONNECT_DONE`, data);
      // this.id = data.id; //  ChatId Chat instance  >    enable
      this.onChatConnect && this.onChatConnect(data);
    });

    this.socket.on(ChattyEvent.CONNECT_FAIL, (data: ChatConnectResponseType) => {
      console.warn(`:: ChattyChat CONNECT_FAIL error`, data?.error.message);
      this.onChatConnect && this.onChatConnect(data);
    });

    this.socket.on(ChattyEvent.FETCH_MESSAGES_DONE, (data: MessagesFetchResponseType) => {
      console.debug(':: ChattyChat FETCH_MESSAGES_DONE', data);
      this.onMessagesFetch && this.onMessagesFetch(data);

      this.markAsRead();
    });

    this.socket.on(ChattyEvent.FETCH_MESSAGES_FAIL, (data: MessagesFetchResponseType) => {
      console.warn(`:: ChattyChat FETCH_MESSAGES_FAIL error`, data?.error.message);
      this.onMessagesFetch && this.onMessagesFetch(data);
    });

    this.socket.on(ChattyEvent.SEND_MESSAGE_DONE, (data: MessageSendResponseType) => {
      console.debug(':: ChattyChat SEND_MESSAGE_DONE', data);
      this.onMessageSend && this.onMessageSend(data);

      //       ChatList .
      if (this.chattyList?.socket) {
        this.chattyList.updateChatList(data);
      }
    });

    this.socket.on(ChattyEvent.SEND_MESSAGE_FAIL, (data: MessageSendResponseType) => {
      console.warn(`:: ChattyChat SEND_MESSAGE_FAIL error`, data?.error.message);
      this.onMessageSend && this.onMessageSend(data);
    });

    this.socket.on(ChattyEvent.SEND_MESSAGE_RETRY, (data: { retry: number }) => {
      console.debug(':: ChattyChat SEND_MESSAGE_RETRY', data);
      this.socket?.emit(ChattyEvent.SEND_MESSAGE, data);
    });

    this.socket.on(ChattyEvent.RECEIVE_MESSAGE, (data: MessageReceiveResponseType) => {
      console.debug(':: ChattyChat NEW_MESSAGE', data);
      this.onMessageReceive && this.onMessageReceive(data);

      this.markAsRead();
    });

    this.socket.on(ChattyEvent.MARK_AS_READ_DONE, (data: MessageSendResponseType) => {
      console.debug(':: ChattyChat MARK_AS_READ_DONE', data);

      // ChatList  ChatList .
      if (this.chattyList?.socket) {
        this.chattyList.updateChatList(data);
      }
    });
    this.socket.on(ChattyEvent.MARK_AS_READ_FAIL, (data: MessageSendResponseType) => {
      console.warn(':: ChattyChat MARK_AS_READ_FAIL', data?.error.message);
    });

    this.socket.on(ChattyEvent.MARK_AS_READ_BYPASS, () => {
      // MARK_AS_READ_DONE   data MARK_AS_READ_BYPASS  . 
      // member  SUPER  MARK_AS_READ   bypass 
      console.debug(':: ChattyChat MARK_AS_READ_BYPASS');
    });

    /**
     * UPDATE_MESSAGES    
     * server   MARK_AS_READ_DONE  
     * MARK_AS_READ   
     * 1.  Send Message  NEW_MESSAGE  
     * 2.  Chat   FETCH_MESSAGES 
     */
    this.socket.on(ChattyEvent.UPDATE_MESSAGES, (data: MessagesUpdateResponseType) => {
      console.debug(':: ChattyChat UPDATE_MESSAGES');
      this.onMessagesUpdate && this.onMessagesUpdate(data);
    });

    this.socket.on(ChattyEvent.REFRESH_CHAT_DONE, (data: ChatRefreshResponseType) => {
      console.debug(`:: ChattyChat REFRESH_CHAT_DONE`, data);
      this.onChatRefresh && this.onChatRefresh(data);
    });

    this.socket.on(ChattyEvent.REFRESH_CHAT_FAIL, (data: ChatRefreshResponseType) => {
      console.warn(`:: ChattyChat REFRESH_CHAT_FAIL error`, data?.error.message);
      this.onChatRefresh && this.onChatRefresh(data);
    });
  }



  private removeListener() {
    if (!this.socket) {
      console.warn(':: ChattyChat socket is not connected');
      return;
    }

    this.socket.off(ChattyEvent.CONNECT_DONE);
    this.socket.off(ChattyEvent.CONNECT_FAIL);

    this.socket.off(ChattyEvent.FETCH_MESSAGES_DONE);
    this.socket.off(ChattyEvent.FETCH_MESSAGES_FAIL);

    this.socket.off(ChattyEvent.SEND_MESSAGE_DONE);
    this.socket.off(ChattyEvent.SEND_MESSAGE_FAIL);

    this.socket.off(ChattyEvent.SEND_MESSAGE_RETRY);

    this.socket.off(ChattyEvent.RECEIVE_MESSAGE);

    this.socket.off(ChattyEvent.MARK_AS_READ_DONE);
    this.socket.off(ChattyEvent.MARK_AS_READ_FAIL);
    this.socket.off(ChattyEvent.MARK_AS_READ_BYPASS);

    this.socket.off(ChattyEvent.REFRESH_CHAT_DONE);
    this.socket.off(ChattyEvent.REFRESH_CHAT_FAIL);

    this.socket = undefined;

  }

}